html {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;

          /*background-color: white;*/
          background-color: #D9E8F6;
          font-family: 'Arial',Arial,sans-serif;
}
*, *::before, *::after { box-sizing: inherit;}

body, h1, h2, p {margin: 0}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
/*
main {
  width: 100vw;
  min-height: 100vh;
  display: flex;
}
*/
main {
  min-height: 100vh;
  display: flex;
}

main.desktop {
  width: 100vw;
}

.decoding_new {
  /*font-size: 50px;*/
  font-weight: bold;
  /*letter-spacing: 10px;*/
  /*height: 100px;*/
  width: 320px;

  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 195px;
  border-radius: 10px;
  background-color: #5900cc;
  padding: 10px 20px;
  color: white;
  text-transform: uppercase;
  text-decoration: none;
}

.decoding__info p {
  padding-bottom: 7px;
}

h1 {
  margin-bottom: 20px;
  font-size: 16px;
}

.mobile h1 {
  margin-bottom: 5vw;
  font-size: 4vw;
}

h1 b {text-transform: uppercase;}

h2 {
  font-size: 20px;
  margin-bottom: 15px;
  text-transform: uppercase;
  text-align: center;
}

.h2margin {
  margin-top: 25px;
  margin-bottom: 0px;
}

.mobile h2 {
  font-size: 5vw;
  margin-bottom: 4vw;
}

.red-text {color: red}
.green-text {color: green}
.blue-text {color: blue}

.sum {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 10px 20px 30px;
  border: 1px solid black;
  width: 300px;
}

.inputs {
  display: flex;
  justify-content: space-between;
  width: 240px;
  margin-bottom: 30px;
  background-color: #D9E8F6;
}

.inputs input {
  width: 65px;
  border: 2px solid #beb9cd;
  border-radius: 10px;
  text-align: center;
  padding: 5px;
  background-color: #D9E8F6;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 195px;
  border-radius: 10px;
  background-color: #5900cc;
  padding: 10px 20px;
  color: white;
  text-transform: uppercase;
  text-decoration: none;
}

.button--red {
  min-width: auto;
  margin-top: 15px;
  background-color: red;
}

.button-anew {margin-right: 35px}

.result {
  display: flex; 
  flex-direction: column;
  align-items: center;
    margin: auto;
}

.container {display: flex;}

.container.column {
  flex-direction: column;
}

.container.column.center{
  align-items: center;
}

.data {
  margin-top: 30px;
  margin-bottom: 80px;
}

.buttons {margin-bottom: 15px;}

.figure {
  position: relative;
  counter-reset: point;    
  background-image: url(bg.svg);
  /*border: 1px black solid;*/
  width: 300px; 
  height: 247px;
  margin-right: 80px;
  background-size: contain; 
  background-repeat: no-repeat;
}

.information {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 24px;
}

.information_value {
  font-weight: bold;
  padding-left: 10px;
}

.point,
.point-sum {
  position: absolute;
  line-height: 100%;
  font-weight: bold;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 50% 50% 50% 50%;
  border: 1px black solid;
  width: 57px;
  height: 57px;
}

.mobile .point {
  width: 14vw;
  height: 14vw;
}

.point div {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  width: 57px;
  height: 57px;
}

.mobile .point div {
  width: 14vw;
  height: 14vw;
}

.point {font-size: 32px;}
.point-sum {font-size: 20px;}


.point-1,
.point-2,
.point-3 {top: 91%;}

.point-1 {left: 8%; background-color: #e43a20;}
.point-2 {left: 50%; background-color: #fbb11e;}
.point-3 {left: 92%; background-color: #f8f64c;}


.point-4,
.point-5,
.point-7 {top: 50%;}

.point-4 {left: 8%; background-color: #82bd53;}
.point-7 {left: 50%; background-color: #da68fe; width: 75px; height: 75px;}
.point-5 {left: 92%; background-color: #1fc9fc;}

.point-7 div {
  width: 75px;
  height: 75px;
}
.mobile .point-7 {
  width: 19vw;
  height: 19vw;
}
.mobile .point-7 div {
  width: 19vw;
  height: 19vw;
}


.point-6,
.point-8,
.point-9 {top: 9%;}

.point-8 {left: 8%; background-color: #9f9f9f;}
.point-6 {left: 50%; background-color: #1b6bf6;}
.point-9 {left: 92%; background-color: #9f9f9f;}




.point-3.point--two-numbers {right: 11%;}
.point-4.point--two-numbers {left: 35%;}
.point-5.point--two-numbers  {right: 25%;}
.point-7.point--two-numbers {right: -8%;}

.point-11 {top: 8%;}

