.decoding {
  width: 100%;
  padding: 20px;
  margin: auto;
}

.decoding h3 {
  text-transform: uppercase;
}

.decoding .button {
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 10px;
  height: 100px;
  width: 320px;
}

.mobile .decoding .button {
  font-size: 7vw;
  font-weight: bold;
  letter-spacing: 2vw;
  height: 10vw;
  width: 100%;
}

.decoding__info {
  margin-top: 15px;
}

.decoding__action {
  justify-content: center;
}

.decoding__container {
  justify-content: space-around;
}
main.mobile {
  padding: 5vw 0;
}
.mobile h1 {
  margin-bottom: 5vw;
  font-size: 4vw;
}
.mobile h2 {
  font-size: 5vw;
  margin-bottom: 4vw;
}
.mobile .sum {
  padding: 2.5vw 5px 7.5vw;
  border: 1px solid black;
  width: 90vw;
}
.mobile .result {
  margin: auto;
}
.mobile .inputs {
  width: 70vw;
  margin-bottom: 7.5vw;
}
.mobile .inputs input {
  width: 20vw;
}
.mobile .button {
  min-width: 70vw;
  font-size: 4vw;
}
.mobile .data {
  flex-direction: column;
  align-items: center;
  margin-top: 7.5vw;
  margin-bottom: 15vw;
}
.mobile .buttons {
  flex-direction: column;
  align-items: center;
  margin-bottom: 4vw;
}

.mobile  .button-anew {
  margin-right: 0;
  margin-bottom: 4vw;
}

.mobile .figure {
  width: 90vw;
  height: 74vw;
  margin-right: 0;
  margin-bottom: 20vw;
}
.mobile .information {
  font-size: 6vw;
}
.mobile .information_value {
  padding-left: 2.5vw;
}
.mobile .point {
